          <text class="no-goods-desc">ÁÇπÂáª‰∏äÊñπ"Êñ∞Â¢ûÁâ©ÂìÅ"ÊåâÈíÆÊ∑ªÂä†‰ª£ÂäûÁâ©ÂìÅ</text>
        </view>
