        <text class="disabled-title">‰ª£ÂäûÁâ©ÂìÅÊ®°ÂùóÂ∑≤Á¶ÅÁî®</text>
        <text class="disabled-desc">ÂêØÁî®ÂêéÂèØÂú®ÂâçÂè∞ÊòæÁ§∫‰ª£ÂäûÁâ©ÂìÅÈÄâÊã©ÂäüËÉΩ</text>
      </view>
