<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

// ÂØºÂÖ•‰∫ëÂØπË±°
const jointManagement = uniCloud.importObject('joint-management')

export default {
  components: { uniPopup },
  
  data() {
    return {
      goods: [],
      isEdit: false,
      editIndex: -1,
      projectId: '',
      projectName: '',
      moduleEnabled: false,
      currentGood: {
        _id: '',
        name: '',
        description: '',
        price: '',
        stock: '',
        enabled: true
      }
    }
  },
  
  onLoad(options) {
    console.log('„ÄêË∞ÉËØï-onLoad„ÄëÊé•Êî∂Âà∞ÁöÑ options =', options);
    this.projectId = options.projectId || '';
    this.projectName = options.projectName || '';
    console.log('„ÄêË∞ÉËØï-onLoad„ÄëËÆæÁΩÆÂêéÁöÑ projectId =', this.projectId);
    console.log('„ÄêË∞ÉËØï-onLoad„ÄëËÆæÁΩÆÂêéÁöÑ projectName =', this.projectName);
    if (!this.projectId) {
      uni.showToast({ title: 'Áº∫Â∞ëÈ°πÁõÆIDÔºåÊó†Ê≥ïÁÆ°ÁêÜÁâ©ÂìÅ', icon: 'none' });
      // ÂèØÈÄâÔºöË∑≥ËΩ¨ÂõûÈ°πÁõÆÂàóË°®È°µ
      return;
    }
    this.loadModuleConfig();
    this.loadGoodsConfig();
  },
  
  methods: {
    async loadModuleConfig() {
      try {
        const result = await jointManagement.getGoodsConfig()
        
        if (result.success && result.data) {
          this.moduleEnabled = result.data.enabled || false
        } else {
          this.moduleEnabled = false
        }
      } catch (error) {
        console.error('Âä†ËΩΩ‰ª£ÂäûÁâ©ÂìÅÊ®°ÂùóÈÖçÁΩÆÂ§±Ë¥•:', error)
        this.moduleEnabled = false
      }
    },
    
    async onModuleToggle(e) {
      try {
        const result = await jointManagement.updateGoodsConfig({ 
          enabled: e.detail.value
        })
        if (result.success) {
          this.moduleEnabled = e.detail.value
          uni.showToast({ title: e.detail.value ? 'Ê®°ÂùóÂ∑≤ÂêØÁî®' : 'Ê®°ÂùóÂ∑≤Á¶ÅÁî®', icon: 'success' })
        } else {
          uni.showToast({ title: result.message, icon: 'none' })
        }
      } catch (error) {
        console.error('Êõ¥Êñ∞Ê®°ÂùóÁä∂ÊÄÅÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Êìç‰ΩúÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async loadGoodsConfig() {
      try {
        const projectId = this.projectId;
        console.log('üîç Âä†ËΩΩÁâ©ÂìÅÈÖçÁΩÆÔºåÈ°πÁõÆID:', projectId);
        
        if (!projectId) {
          console.error('üîç Áº∫Â∞ëÈ°πÁõÆIDÔºåÊó†Ê≥ïÂä†ËΩΩÁâ©ÂìÅÈÖçÁΩÆ');
          uni.showToast({ title: 'Áº∫Â∞ëÈ°πÁõÆID', icon: 'none' });
          return;
        }
        
        const result = await jointManagement.getGoods({ projectId })
        console.log('üîç Ëé∑ÂèñÁâ©ÂìÅÈÖçÁΩÆÁªìÊûú:', result);
        
        if (result.success && result.data) {
          this.goods = result.data
          console.log('üîç Âä†ËΩΩÂà∞ÁöÑÁâ©ÂìÅÂàóË°®:', this.goods)
        } else {
          console.error('üîç Ëé∑ÂèñÁâ©ÂìÅÈÖçÁΩÆÂ§±Ë¥•:', result.message);
          this.goods = []
          if (result.message) {
            uni.showToast({ title: result.message, icon: 'none' })
          }
        }
      } catch (error) {
        console.error('üîç Âä†ËΩΩÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÈÖçÁΩÆÂ§±Ë¥•:', error)
        this.goods = []
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    // Ê®°ÂùóÂºÄÂÖ≥ÂäüËÉΩÂ∑≤ÁßªÈô§ÔºåÁé∞Âú®Âü∫‰∫éÈ°πÁõÆÁÆ°ÁêÜÁâ©ÂìÅ
    
    showAddModal() {
      this.isEdit = true
      this.editIndex = -1
      this.currentGood = {
        _id: '',
        name: '',
        description: '',
        price: '',
        stock: '',
        enabled: true
      }
    },
    
    editGood(index) {
      this.isEdit = true
      this.editIndex = index
      this.currentGood = JSON.parse(JSON.stringify(this.goods[index]))
    },
    
    closeModal() {
      this.isEdit = false
      this.editIndex = -1
      this.currentGood = {
        _id: '',
        name: '',
        description: '',
        price: '',
        stock: '',
        enabled: true
      }
    },
    
    onGoodSwitchChange(e) {
      this.currentGood.enabled = e.detail.value
    },
    
    async saveGood() {
      if (!this.currentGood.name || !this.currentGood.price) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂøÖÂ°´È°π', icon: 'none' })
        return
      }
      
      try {
        const data = {
          ...this.currentGood,
          price: Number(this.currentGood.price) || 0,
          stock: this.currentGood.stock ? Number(this.currentGood.stock) : null
        }
        const projectId = this.projectId;
        console.log('üîç ‰øùÂ≠òÁâ©ÂìÅÔºåÈ°πÁõÆID:', projectId, 'Áâ©ÂìÅÊï∞ÊçÆ:', data)
        
        if (!projectId) {
          uni.showToast({ title: 'Áº∫Â∞ëÈ°πÁõÆIDÔºåÊó†Ê≥ï‰øùÂ≠ò', icon: 'none' });
          return;
        }
        
        let result
        if (this.editIndex !== -1) {
          // Êõ¥Êñ∞Áâ©ÂìÅ
          console.log('üîç Êõ¥Êñ∞Áâ©ÂìÅÔºåÁ¥¢Âºï:', this.editIndex)
          result = await jointManagement.updateGood({
            projectId,
            goodIndex: this.editIndex,
            good: data
          })
        } else {
          // Ê∑ªÂä†Áâ©ÂìÅ
          console.log('üîç Ê∑ªÂä†Áâ©ÂìÅ')
          result = await jointManagement.addGood({
            projectId,
            good: data
          })
        }
        
        console.log('üîç ‰øùÂ≠òÁªìÊûú:', result)
        
        if (result.success) {
          await this.loadGoodsConfig();
          console.log('üîç ‰øùÂ≠òÂêéÁâ©ÂìÅÂàóË°®:', this.goods)
          this.closeModal();
          uni.showToast({ title: this.editIndex !== -1 ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü', icon: 'success' })
        } else {
          throw new Error(result.message || '‰øùÂ≠òÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('üîç ‰øùÂ≠òÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÂ§±Ë¥•:', error)
        uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•: ' + (error.message || error), icon: 'none' })
      }
    },
    
    async deleteGood(index) {
      const projectId = this.projectId;
      console.log('üîç Âà†Èô§Áâ©ÂìÅÔºåÈ°πÁõÆID:', projectId, 'Áâ©ÂìÅÁ¥¢Âºï:', index)
      
      if (!projectId) {
        uni.showToast({ title: 'Áº∫Â∞ëÈ°πÁõÆIDÔºåÊó†Ê≥ïÂà†Èô§', icon: 'none' });
        return;
      }
      
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Áâ©ÂìÅÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              console.log('üîç ÊâßË°åÂà†Èô§Êìç‰Ωú')
              const result = await jointManagement.deleteGood({
                projectId,
                goodIndex: index
              })
              
              console.log('üîç Âà†Èô§ÁªìÊûú:', result)
              
              if (result.success) {
                await this.loadGoodsConfig();
                console.log('üîç Âà†Èô§ÂêéÁâ©ÂìÅÂàóË°®:', this.goods)
                uni.showToast({ title: 'Âà†Èô§ÊàêÂäü', icon: 'success' })
              } else {
                uni.showToast({ title: result.message || 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
              }
            } catch (error) {
              console.error('üîç Âà†Èô§ÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÂ§±Ë¥•:', error)
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•: ' + (error.message || error), icon: 'none' })
            }
          }
        }
      })
    }
  }
}
</script>

